import { FormErrors } from '../errors/validation.error';
import { EmployeeForm } from '../forms/employee.forms';
import { createValidator, ValidationRules } from '../utils/validation.utils';

export const validateEmployeeForm = (form: EmployeeForm): FormErrors<EmployeeForm> => {
    const errors: FormErrors<EmployeeForm> = {};

    errors.fullName = createValidator([
        { check: (v) => ValidationRules.required(v), message: 'Full name is required' },
        { check: (v) => ValidationRules.minLength(v, 2), message: 'Full name must be at least 2 characters' },
        { check: (v) => ValidationRules.maxLength(v, 100), message: 'Full name must be less than 100 characters' }
    ])(form.fullName);

    errors.email = createValidator([
        { check: (v) => !v || ValidationRules.email(v), message: 'Please enter a valid email address' }
    ])(form.email);

    errors.phone = createValidator([
        { check: (v) => !v || ValidationRules.phone(v), message: 'Please enter a valid phone number' }
    ])(form.phone);

    if (!form.email && !form.phone) {
        errors.email = errors.email || 'Either email or phone is required';
        errors.phone = errors.phone || 'Either phone or email is required';
    }
    
    errors.department = null;
    errors.role = null;

    return errors;
};